<template>
  <section id="eventSl">
    <h2 class="sub_title">EVENT</h2>
    <swiper
      :slides-per-view="1.3"
      :space-between="16"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
      class="eventSlider">
      <swiper-slide><router-link to="/interior/event"><img src="../assets/img/slide1.jpg" alt="" /></router-link></swiper-slide>
      <swiper-slide><router-link to="/interior/event"><img src="../assets/img/slide2.jpg" alt="" /></router-link></swiper-slide>
      <swiper-slide><router-link to="/interior/event"><img src="../assets/img/slide1.jpg" alt="" /></router-link></swiper-slide>
    </swiper>
  </section>
  <section id="gallerySl">
    <h2 class="sub_title">오늘의 인기 사진</h2>
    <swiper
      :slides-per-view="2"
      :space-between="16"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
      class="eventSlider"
    >
      <swiper-slide v-for="(a, i) in 5" :key="i">
        <div class="imgBox">
        <div class="bgBox" :style="{ backgroundImage: `url(${imgArray[i].imgUrl})` }"></div>
        <button @click="scrapBtn(imgArray[i]), getLocal(), imgToggle(), scarpToggle(imgArray[i])" :class="{ active: imgArray[i].scrap }"></button>
        <div class="shadowBox"></div>
        <span>{{ a.title }}</span>
      </div>
      </swiper-slide>
     
    </swiper>
  </section>
  <section id="post">
    <h2 class="sub_title">오늘의 인기 포스트</h2>
    <Post :data="data[i]" v-for="(a, i) in 3" :key="i" />
  </section>

</template>
<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import Post from "./Post.vue";
import "swiper/swiper.scss";
import { mapMutations, mapState } from "vuex";

export default {
  name: "home",
  computed: {
    ...mapState(["data","isDetail","imgArray"]),
  },
  methods:{
    ...mapMutations(["detail", "resetDetail","scrapBtn","getLocal", "imgToggle","scarpToggle"]),
    toggleBtn(i){
      this.linkToggle = [false, false, false, false];
      this.linkToggle[i] = true;
      this.title = this.titleArray[i];
    }
  },
  components: {
    Swiper,
    SwiperSlide,
    Post,
  },
  mounted(){
    this.resetDetail();
    console.log(this.isDetail)
  },
  data() {
    return {
      linkToggle : [true, false, false, false],
      titleArray:["Home", "Gallery", "Guide", "Q&A"],
      title:"Home"
    };
  },
};
</script>
