<template>
  <section id="gallery">
      <h2 class="sub_title">오늘의 인기 사진</h2>
      <div class="container">
           <div class="odd">
               <div class="imgBox"  v-for="(a,i) in imgArray" :key="i">
                  <img :src="a.imgUrl" alt=""  v-if="(i % 2 == 0)">
                  <button @click="scrapBtn(a), scarpToggle(a)" :class="{active : a.scrap}"></button>
                  <div class="shadowBox"></div>
                  <span>{{a.title}}</span>
               </div>
           </div>
          <div class="even">
              <div class="imgBox"  v-for="(a,i) in imgArray" :key="i">
                  <img :src="a.imgUrl" alt=""  v-if="(i % 2 != 0)">
                  <div class="shadowBox"></div>
                  <button @click="scrapBtn(a), scarpToggle(a)" :class="{active : a.scrap}"></button>
                  <span>{{a.title}}</span>
               </div>
          </div>
      </div>

  </section>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
export default {
    name:'gallery',
    data(){
        return{
        }
    },
    computed:{
        ...mapState(["scrapArray", "imgArray", "sArray"])
    },
    methods:{
        alert(a,e){
            e.preventDefault();
            alert(a);
        },
        ...mapMutations(["scrapBtn","scarpToggle"])
    }
}
</script>

