import {
  createStore
} from 'vuex'
import Data from './assets/data.js'
const store = createStore({
  state() {
    return {
      name: 'jeong',
      data: Data,
      isHeart: false,
      isDetail: false,
      follow: [],
      fArray: [],
      isFollow: true,
      isScrap: true,
      imgArray: [{
          imgUrl: require("./assets/img/g1.jpg"),
          title : "빵순이의 즐거운 빵모닝",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g2.jpg"),
          title : "햇살 가득한 환한 거실",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g3.jpg"),
          title : "햇빛가득♥️",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g4.jpg"),
          title : "초록이들이 건강하게 자라주길",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g5.jpg"),
          title : "예쁘게 핀 오하우스 꽃",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g6.jpg"),
          title : "플랜테리어",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g7.jpg"),
          title : "평화로운 오후",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g8.jpg"),
          title : "독서하기 좋은 거실",
          scrap : false,
        },
        {
          imgUrl: require("./assets/img/g9.jpg"),
          title : "귀여운거 다 모아모아",
          scrap : false,
        },
      ],
      scrapArray:[],
      sArray:[],
    }
  },
  mutations: {
    detail(state) {
      state.isDetail = true;
    },
    resetDetail(state) {
      state.isDetail = false;
    },
    followBtn(state, e) {
      for (let i = 0; i < state.data.length; i++) {
        if (state.data[i].name == e.name && state.data[i].isFollow == false) {
          state.data[i].isFollow = true;
          state.follow.push(state.data[i]);
        } else if (state.data[i].name == e.name && state.data[i].isFollow == true) {
          state.data[i].isFollow = false;
          state.follow = state.follow.filter((item) => {
            return item.name != e.name;
          })
        }
      }
      state.follow = state.follow.sort(function (a, b) {
        return new Date(b.date) - new Date(a.date)
      });
      localStorage.setItem("follow", JSON.stringify(state.follow));
    },
    getLocal(state) {
      let output = localStorage.getItem("follow");
      state.fArray = JSON.parse(output);
      let output2 = localStorage.getItem("scrap");
      state.sArray = JSON.parse(output2);
    },
    scrapBtn(state, e) {
      if(e.scrap == false){
        e.scrap =true;
        state.scrapArray.unshift(e);
        console.log(e.scrap)
      }
      else{
        e.scrap =false;
        state.scrapArray = state.scrapArray.filter((item) => {
                return item.title != e.title;
              });
              console.log(e.scrap)
      }
      localStorage.setItem("scrap", JSON.stringify(state.scrapArray));
    },
    followToggle(state) {
      if (state.fArray.length == 0) {
        state.isFollow = true;
      } else {
        state.isFollow = false;
      }
    },
    imgToggle(state) {
      if (state.sArray.length == 0) {
        state.isScrap = true;
      } else {
        state.isScrap = false;
      }
    },
    scarpToggle(state, e){
      for(let i=0; i < state.imgArray.length; i++){
        if(state.imgArray[i].title == e.title){
          state.imgArray[i].scrap = e.scrap;
          console.log(state.imgArray[i].scrap)
        }
      }
    }
  }
})

export default store